.video-post-page {
    background-color: #243743;
    padding: 20px;
    min-height: 100vh;
}

.video-post-title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 5%;
    color: white;
    background: linear-gradient(to right, rgba(100, 250, 1, 0.84), #eee6e6);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.add-post-button {
    position: absolute;
    top: 30%;
    right: 5%;
    width: 15%;
    color: white;
    border: 2px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    border-radius: 50px;
    padding: 10px 20px;
    font-size: 15px;
    cursor: pointer;
}

.popup {
    border: 4px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    color: white;
    z-index: 9999;
    width: 30%;
}

.popup-content {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    border-radius: 20px;
}

.popup-content label,
.popup-content input {
    margin-bottom: 10px;
}

.popup-content input {
    height: 30px;
}

.popup-content div {
    display: flex;
    justify-content: space-between;
}

.add-post-button:hover {
    color: white;
    border: 2px solid white;
    background-color: #9987eb;
}

.popup-button {
    margin-top: 5%;
}

.popup-btn {
    color: white;
    border: 2px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    border-radius: 50px;
    padding: 10px 20px;
    font-size: 15px;
    cursor: pointer;
}

.popup-btn:hover {
    color: white;
    border: 2px solid white;
    background-color: #9987eb;
}

.post-container {
    margin-top: 11%;
    border: 4px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    border-radius: 50px;
    justify-content: center;
}

.post-item {
    /*background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);*/
    /*background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);*/
    background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
    /*background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);*/
    border-radius: 50px;
    width: 95%;
    height: 150px;
    margin: 20px auto; /* 중앙 정렬 및 위아래 여백 추가 */
    padding: 20px; /* 내부 여백 추가 */
}

.like-unlike-container {
    display: flex;
    flex-direction: column;
    width: 120px;
    gap: 20px;
    margin: 20px;
}

.like-btn, .unlike-btn {
    color: white;
    border: 3px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    border-radius: 50px;
    padding: 10px 20px;
    font-size: 15px;
    cursor: pointer;
}

.like-btn:hover, .unlike-btn:hover {
    color: white;
    border: 2px solid white;
    background-color: #9987eb;
}

.thumbnail-img {
    max-width: 200px;
    max-height: 200px;
    display: block;
    margin-left: 180px;
    margin-top: -155px;
    border-radius: 30px;
}

.post-item-text {
    margin-left: 30%;
    margin-top: -170px;
    display: flex;
    justify-content: space-between;
}

.post-info-item-1 {
    position: absolute;
    flex: 1;
    text-align: center;
    margin-top: 100px;
}
.post-info-item-2 {
    position: absolute;
    flex: 1;
    text-align: center;
    margin-top: 100px;
    gap: 20px;
}

.post-delete-btn {
    position: absolute;
    margin-top: 5%;
    margin-left: 50%;
    color: white;
    border: 3px solid #9987eb;
    background-color: rgba(40, 44, 52, 0.85);
    border-radius: 20px;
    padding: 10px 20px;
    font-size: 15px;
    cursor: pointer;
}

.post-delete-btn:hover {
    color: white;
    border: 2px solid white;
    background-color: #9987eb;
}